/* 글로벌 스타일 */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* 헤더 스타일 */
.App-header {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem;
}

.App-header h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

/* 메인 컨텐츠 스타일 */
.Main-content {
  flex: 1;
  padding: 1rem;
}

.Analysis {
  margin-bottom: 2rem;
  text-align: center;
}

.Analysis h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.Analysis-table {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  border-collapse: collapse;
}

.Analysis-table th,
.Analysis-table td {
  border: 1px solid #ddd;
  padding: 0.75rem;
  text-align: center;
}

.Analysis-table th {
  background-color: #333;
  color: white;
}

.Analysis-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.Features {
  text-align: center;
}

.Features ul {
  list-style: none;
  padding: 0;
}

.Features li {
  margin: 0.5rem 0;
}

/* 푸터 스타일 */
.Footer {
  text-align: center;
  padding: 1rem;
  background-color: #333;
  color: white;
}

/* 반응형 디자인 */
@media (min-width: 768px) {
  .App-header h1 {
    font-size: 2.5rem;
  }

  .Analysis h2 {
    font-size: 2rem;
  }

  .Analysis-table th,
  .Analysis-table td {
    font-size: 1rem;
  }
}
